.all-events {
  padding: 20px;
  background-color: #f9f9f9;
  border-radius: 10px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  font-family: 'om';
}

.all-events button{
  font-family: 'om';
}
.all-events h2 {
  text-align: center;
  color: #000;
}

.all-events input[type="text"] {
  width: 90%;
  padding: 10px;
  margin: 10px 0;
  border: 1px solid #ddd;
  border-radius: 5px;
}

.genre-filters {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  margin: 10px 0;
}

.genre-filters label {
  display: flex;
  align-items: center;
  gap: 5px;
}

.date-clear-filters {
  display: flex;
  align-items: center;
  gap: 10px;
  margin: 10px 0;
}

.all-events input[type="date"] {
  flex: 1;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 5px;
}

.all-events button {
  padding: 10px 20px;
  background-color: #ebb319;
  color: #000;
  border: none;
  border-radius: 5px;
  font-size: 16px;
  cursor: pointer;
  margin: 0;
}

.all-events button:hover {
  background-color: #d49d17;
}

.all-events ul {
  list-style: none;
  padding: 0;
}

.all-events ul li {
  padding: 10px;
  margin: 10px 0;
  border: 1px solid #ddd;
  border-radius: 5px;
  background-color: #fff;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.05);
}

.all-events ul li h3 {
  margin: 0;
  color: #000;
}

.all-events ul li p {
  margin: 5px 0;
  color: #555;
}
.all-events {
  width: 95%;
  padding: 20px;
}

.all-events h2 {
  margin-bottom: 20px;
}

.all-events input[type="text"] {
  width: 97%;
  padding: 10px;
  margin-bottom: 20px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.filters {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
}

.genre-filters,
.type-filters {
  flex: 1;
  margin-right: 20px;
}

.date-clear-filters {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.date-clear-filters input[type="date"] {
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.date-clear-filters button {
  padding: 10px 20px;
  border: none;
  background-color: #f58318;
  color: white;
  border-radius: 4px;
  cursor: pointer;
}

.date-clear-filters button:hover {
  background-color: #d47213;
}

.all-events ul {
  list-style: none;
  padding: 0;
}

.all-events li {
  padding: 20px;
  border-bottom: 1px solid #ccc;
  margin-bottom: 20px;
}

.all-events li h3 {
  margin: 0 0 10px 0;
}

.all-events li p {
  margin: 5px 0;
}

.all-events li button {
  padding: 10px 20px;
  border: none;
  background-color: #f58318;
  color: white;
  border-radius: 4px;
  cursor: pointer;
}

.all-events li button:hover {
  background-color: #d47213;
}