.ticket-purchase {


  font-family: 'om';
}

.ticket-purchase h2, .ticket-purchase h3 {
  text-align: center;
  color: #000;
}

.ticket-purchase input {
  width: 95%;
  padding: 10px;
  margin: 5px 10px;
  border: 1px solid #ddd;
  border-radius: 5px;
}

.ticket-purchase button {
  width: 30%;
  padding: 10px;
  background-color: #ebb319;
  color: #000;
  border: none;
  border-radius: 5px;
  font-size: 16px;
  cursor: pointer;
  margin-top: 20px;
  margin-bottom: 10px;
  font-family: 'om';
  font-weight: bold;
 
}

.ticket-purchase button:hover {
  background-color: #d49d17;
}

.ticket-purchase .form-group {
  display: flex;
  justify-content: space-between;
  gap: 20px;
}

.ticket-purchase .form-group input {
  flex: 1;
}

.ticket-purchase .form-group input:not(:last-child) {
  margin-right: 20px;
}

@media (max-width: 768px) {
  .ticket-purchase .form-group {
    flex-direction: column;
  }

  .ticket-purchase .form-group input {
    margin-right: 0;
  }
}


.ticket-purchase h2{
  font-size: 30px;
}
