header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 70 50px; /* Adjust the padding as needed */
  background-color: #ebb319;
  color: #000;
  position: relative; /* Ensure positioning context for absolute elements */
  padding-top: 70px;
  padding-bottom: 70px;
}

nav {
  display: flex;
  align-items: center;
  width: 100%;
  justify-content: space-between;
}

nav.left,
nav.right {
  flex: 1;
  display: flex;
  justify-content: space-around;
}

nav ul {
  display: flex;
  list-style: none;
  padding: 0;
  margin: 0;
}

nav ul li {
  cursor: pointer;
  font-size: 30px;
  font-family: 'om';
  font-weight: 600;
}

.logo-container {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 70px;
}

.logo {
  height: 140px; 
  padding: 10px 70px 10px 0; 
}

nav ul li:hover {
  text-decoration: underline;
}
