.venue {
  padding: 20px;
  text-align: center;
}

.venue h2 {
  font-family: 'om';
  font-size: 40px;
  margin-bottom: 30px;
  color: #333;
}

.flexbox-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.stage {
  margin: 20px;
  padding: 20px;
  border: 1px solid #ddd;
  border-radius: 8px;
  background-color: #fff;
  width: 30%;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  text-align: left;
}

.stage img {
  width: 100%;
  height: auto;
  border-radius: 8px;
}

.stage button {
  margin-top: 10px;
  padding: 10px 20px;
  border: none;
  border-radius: 4px;
  background-color: #ebb319;
  color: #fff;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.stage button:hover {
  background-color: #d4980d;
}

.stage p {
  margin-top: 10px;
  font-size: 1em;
  color: #555;
}
